<template>
  <child-menu :menuList="menuList"></child-menu>
</template>

<script>
  import ChildMenu from '@comm/components/childMenu/home.vue'
  export default {
    components:{
      ChildMenu
    },
    data() {
      return {
        menuList:[
          {
            name:'物品统计',
            path:'/goodsStatistics',
            childName:'goodsStatistics'
          },
          {
            name:'入库明细',
            path:'/inDetails',
            childName:'inDetails'
          },
          {
            name:'出库明细',
            path:'/outDetails',
            childName:'outDetails'
          },
          {
            name:'现存量',
            path:'/standingStock',
            childName:'standingStock'
          },
          {
            name:'预警统计',
            path:'/warningStatistics',
            childName:'warningStatistics'
          }
        ]
      }
    },
    created() {

    },
    methods: {

    }
  }
</script>

<style>

</style>



