<template>
  <child-menu :menuList="menuList" ref="childMenu"></child-menu>
</template>

<script>
  import ChildMenu from '@comm/components/childMenu/home.vue'
  export default {
    components:{
      ChildMenu
    },
    data() {
      return {
        menuList:[
          {
            name:'部门审核',
            path:'/department',
            childName:'department',
            number:''
          },
          {
            name:'总务审核',
            path:'/generalAffairs',
            childName:'generalAffairs',
            number:''
          }
        ]
      }
    },
    computed:{

    },
    created() {
      this.$store.dispatch('getDeptCheckNum');
      this.$store.dispatch('getZWCheckNum');
    },
    methods: {

    },
    watch:{
      "$store.state.departCheckNum": function() {
        this.menuList[0].number = this.$store.state.departCheckNum;
      },
      "$store.state.generalCheckNum": function() {
        this.menuList[1].number = this.$store.state.generalCheckNum;
      }
    },
    mounted(){}
  }
</script>

<style>

</style>



