<template>
  <child-menu :menuList="menuList"></child-menu>
</template>

<script>
  import ChildMenu from '@comm/components/childMenu/home.vue'
  export default {
    components:{
      ChildMenu
    },
    data() {
      return {
        menuList:[
          {
            name:'仓库档案',
            path:'/warehouseFiles',
            childName:'warehouseFiles'
          },
          {
            name:'物品分类',
            path:'/goodsClassify',
            childName:'goodsClassify'
          },
          {
            name:'物品档案',
            path:'/goodsFiles',
            childName:'goodsFiles'
          },
          {
            name:'供应商档案',
            path:'/supplierFiles',
            childName:'supplierFiles'
          },
          {
            name:'申领设置',
            path:'/set',
            childName:'set'
          },
          {
            name:'出入库类型',
            path:'/inAndOutType',
            childName:'inAndOutType'
          }
        ]
      }
    },
    created() {},
    methods:{}
  }
</script>





