import Vue from 'vue'
import ViewUI from 'view-design';
// import {i18n} from '../lang/index.js'

Vue.use(ViewUI, {
	// i18n: (key, value) => i18n.t(key, value),//解决modal弹窗报错
    transfer: true,
    select: {
        arrow: 'md-arrow-dropdown',
        arrowSize: 20
    }
});

export default ViewUI;



