import Vue from 'vue'
import Common from './common/index'// 全局方法
import store from './store/index'// 状态管理
import router from './router/index'// 路由
import '@comm/iscroll/iscroll'// 滚动条
import '@comm/IE'
import App from './app.vue'
import './assets/styles/main.less'// 当前项目自定义样式
import ECharts from 'vue-echarts'
import Print from './components/print'
Vue.component('chart', ECharts)
Vue.use(Common)
Vue.use(Print);
// 设置axios的默认路径
BaseAxios.defaults.baseURL=setAxiosBaseUrl(modulePath.consumablesManage);
//国际化
import {i18n} from "../comm/lang";

new Vue({
  el: '#app',
  router: router,
  store,
  i18n,
  template: '<App/>',
  components: { App },
})




