<template>
  <child-menu :menuList="menuList"></child-menu>
</template>

<script>
  import ChildMenu from '@comm/components/childMenu/home.vue'
  export default {
    components:{
      ChildMenu
    },
    data(){
      return {
        menuList:[
          {
            name:'物品发放',
            path:'/goodsProvide',
            childName:'goodsProvide',
            number:''
          },
          {
            name:'入库',
            path:'/putIn',
            childName:'putIn'
          },
          {
            name:'出库',
            path:'/out',
            childName:'out'
          },
          {
            name:'调拨',
            path:'/allocation',
            childName:'allocation'
          },
          {
            name:'盘点',
            path:'/check',
            childName:'check'
          },
          {
            name:'回收站',
            path:'/recovery',
            childName:'recovery'
          }
        ]
      }
    },
    computed:{
      provideNum(){
        return this.$store.state.provideNum;
      }
    },
    created(){

    },
    methods:{

    },
    mounted(){
      this.$store.dispatch('getProvideNum');
      this.menuList[0].number = this.provideNum;
    },
    watch:{
      "$store.state.provideNum": function() {
        this.menuList[0].number = this.$store.state.provideNum;
      }
    },
  }
</script>

<style>

</style>





