<template>
  <main-menu :menu="menuList"></main-menu>
</template>
<script>
  import MainMenu from '../../comm/components/mainMenu/topBottomMain.vue'

  export default {
    components: {
      MainMenu
    },
    data() {
      return {
        menuList: {
          moduleName: '易耗品管理',//模块名称
          moduleLogo: require('../assets/images/logo.png'),//模块logo
          homePath: '/',//点击logo的路由地址，通常设置为首页地址
          homeParentsName: 'home',//点击logo的路由name，通常设置为首页name
          //导航菜单
          list: [
            {
              name: '首页',
              path: '/home',
              parentsName: 'home',
              access: false // true 有权限 false 无权限
            },
            {
              name: '物品申领',
              path: '/applyGoods',
              parentsName: 'application',
              access: false
            },
            {
              name: '审核',
              path: '/department',
              parentsName: 'approval',
              access: false
            },
            {
              name: '易耗品管理',
              path: '/goodsProvide',
              parentsName: 'consumablesManage',
              access: false
            },
            {
              name: '统计查询',
              path: '/goodsStatistics',
              parentsName: 'statistics',
              access: false
            },
            {
              name: '基础设置',
              path: '/warehouseFiles',
              parentsName: 'setting',
              access: false
            },
          ]
        }
      }
    },
    computed: {
      access(){
        return this.$store.state.access;
      }
    },
    created() {
      this.menuList.list[0].access = this.access.indexOf('sr_storeroom_homepage')>-1;
      this.menuList.list[1].access = this.access.indexOf('sr_personalApply')>-1;
      this.menuList.list[2].access = this.access.indexOf('sr_personalAudit')>-1;
      this.menuList.list[3].access = this.access.indexOf('sr_warehouseManage')>-1;
      this.menuList.list[4].access = this.access.indexOf('sr_statistics')>-1;
      this.menuList.list[5].access = this.access.indexOf('sr_goods')>-1;
    },
    watch: {
      "$store.state.access": function() {
        this.menuList.list[0].access = this.$store.state.access.indexOf('sr_storeroom_homepage')>-1;
        this.menuList.list[1].access = this.$store.state.access.indexOf('sr_personalApply')>-1;
        this.menuList.list[2].access = this.$store.state.access.indexOf('sr_personalAudit')>-1;
        this.menuList.list[3].access = this.$store.state.access.indexOf('sr_warehouseManage')>-1;
        this.menuList.list[4].access = this.$store.state.access.indexOf('sr_statistics')>-1;
        this.menuList.list[5].access = this.$store.state.access.indexOf('sr_goods')>-1;
      },
    },
    methods:{},
    mounted(){}
  }
</script>
<style lang="less">

</style>



