// 自定义滚动条
// import vuescroll from 'vuescroll';//全部引入
import Vue from 'vue'
import vuescroll from 'vuescroll/dist/vuescroll-native'// 按需引入
Vue.use(vuescroll, {
  ops: {
    rail: {
      opacity: '0.2',
      background: 'rgb(209, 224, 235)',
      border: 'none',
      size: '16px',
      specifyBorderRadius: '0',
      gutterOfSide: '0',
    },
    bar: {
      background: 'rgb(209, 224, 235)',
      keepShow: true,
      size: '8px'
    },
    scrollButton: {
      enable: true,
      background: '#cecece'
    },
    scrollPanel: {
      easing: 'easeInQuad',
      speed: 800
    },
    vuescroll: {
      wheelScrollDuration: 0,
      detectResize: false
    }
  }
})



