import Vue from 'vue'
import iView from '@comm/iview/index' // iview
import {canTurnTo} from '../../comm/script/utils/index'
import store from '../store/index'
import VueRouter from 'vue-router'
import routes from './routerConfig'

// 路由配置
Vue.use(VueRouter)
const RouterConfig = {
  mode: 'hash',
  routes
}
const router = new VueRouter(RouterConfig)

const Util = function(title) {
  title = title ? '易耗品管理 - ' + title : '易耗品管理'
  window.document.title = title
}

router.beforeEach((to, from, next) => {
  iView.LoadingBar.start()
  Util(to.meta.title)
  if (to.matched.some(record => record.meta.requiresAuth)) {
    store.dispatch('getBaseInfo').then(res => {
      let accessArr = []
      if(res.operations && res.operations.length>0){
        res.operations.map((item)=>{
          accessArr.push(item.code);
        })
      }
      // 拉取用户信息，通过用户权限和跳转的页面的name来判断是否有权限访问;
      // access必须是一个数组，如：['super_admin'] ['super_admin', 'admin']
      if (canTurnTo(to.name, accessArr, routes)) { // 有权限，可访问
        next()
      } else {
        next({ replace: true, path: '/401' }) // 无权限，重定向到401页面
      }
    }).catch((error) => {
      console.log(error)
    })
  } else {
    next() // 确保一定要调用 next()
  }
})
router.afterEach((to, from, next) => {
  iView.LoadingBar.finish()
  window.scrollTo(0, 0)
})

export default router



