<!--一级导航菜单-->
<template>
    <div class="zd-common-layout">
        <header class="zd-common-pf zd-common-top0 zd-common-wp100 zd-common-zIndex1000 zd-common-white-b zd-common-h60 zd-common-lh60 zd-common-plr83 zd-header">
            <div class="zd-common-clearFix">

              <div class="zd-common-fl zd-common-mr100">
                <a @click="changeHeadNav(menu.homePath,menu.homeParentsName)"
                   class="zd-common-primary-c zd-common-font-weight zd-common-font-size-large">
                  <img :src="menu.moduleLogo" class="zd-common-mr10 zd-common-img-vm zd-common-mt-negative4 zd-common-w40">{{ menu.moduleName }}</a>
              </div>

              <ul class="clearfix zd-common-fr zd-common-assist-font-c zd-common-font-size-large zd-common-nav zd-common-ml50 zd-common-mr-negative16">
                  <li v-for="item in menu.list" @click="changeHeadNav(item.path,item.parentsName)"
                       class="zd-common-fl zd-common-ml12 zd-common-cp zd-common-nav-child" v-if="item.access">
                      <span :class="[curHeadNavActive === item.parentsName ? 'zd-common-active' : '']">{{ item.name }}</span>
                  </li>
              </ul>

              <!--<div class="zd-common-fr zd-common-font-size-base">
                  <span class="zd-common-fl zd-common-mr10">欢迎您，administrator</span>
                  <span class="zd-common-fl zd-common-cp zd-common-primary-c-hover" @click="quitHandle">退出</span>
              </div>-->

            </div>
        </header>
        <!--判断是否有主页面板，主页面板样式与其他栏目使用的<router-view>不同,缺少".zd-common-white-b"类-->
        <div v-if="curHeadNavActive !== menu.homeParentsName"
          class="zd-common-clearFix zd-common-layout-content zd-common-pr zd-common-top80 zd-common-white-b zd-common-minH500">
            <div class="zd-common-topBottom-layout">
                <router-view></router-view>
            </div>
        </div>
        <div v-else class="zd-common-clearFix zd-common-layout-content zd-common-pr zd-common-top80 zd-common-minH500">
          <div class="zd-common-topBottom-layout">
            <router-view></router-view>
          </div>
        </div>
    </div>
</template>
<script>
    export default {
        props:{
          menu:{
            type:Object
          }
        },
        data(){
            return {
                curHeadNavActive:this.menu.homeParentsName,
            }
        },
        created(){
            this.changeHeadNav(this.$route.path,this.$route.meta.parents);
        },
        computed:{
          //通过状态管理获取权限
          /*access(){
            return this.$store.state.access;
          }*/
        },
        watch: {
            // 如果路由有变化，会再次执行该方法
            '$route': 'watchHandle'
        },
        methods:{
            //切换导航选中状态
            watchHandle(){
                this.curHeadNavActive = this.$route.meta.parents;
            },
            changeHeadNav(path,name){
                this.toMenu(path);
                this.curHeadNavActive = name;
            },
            //路由跳转
            toMenu(path){
                this.$router.push(path);
            },
        },
        mounted(){

        }
    }
</script>
<style>

</style>



