import Vue from 'vue'
import Vuex from 'vuex'
// import axios from '../../comm/axios/index'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    access: [],
    userName:'',
    userId:'',
    schoolName:'',
    departCheckNum:'',
    generalCheckNum:'',
    provideNum:0,
  },
  mutations: {
    setAccess (state, access) {
      state.access = access
    },
    setUserName (state, userName) {
      state.userName = userName
    },
    setUserId (state, userId) {
      state.userId = userId
    },
    setSchoolName (state, schoolName) {
      state.schoolName = schoolName
    },
    setDepartCheckNum(state, departCheckNum) {
      state.departCheckNum = departCheckNum
    },
    setGeneralCheckNum(state, generalCheckNum) {
      state.generalCheckNum = generalCheckNum
    },
    setProvideNum(state, provideNum) {
      state.provideNum = provideNum
    },
  },
  actions: {
    // 获取用户相关信息
    getBaseInfo ({ commit }) {
      return new Promise((resolve, reject) => {
        BaseAxios(
          {
            method: 'post',
            url: '/sr/storeroomData!getUserOperation.action',
            data: {}
          })
          .then((response) => {
            let aData = response.data;
            let accessArr = [];
            if(aData.operations && aData.operations.length>0){
              aData.operations.map((item)=>{
                accessArr.push(item.code);
              })
            }
            commit('setAccess', accessArr);
            commit('setUserName', aData.userName);
            commit('setUserId', aData.userId);
            commit('setSchoolName', aData.schoolName);
            resolve(aData)
          }).catch(err => {
            reject(err)
          })
      })
    },
    //获取部门待审核数量
    getDeptCheckNum ({ commit }) {
      return new Promise((resolve, reject) => {
        BaseAxios(
          {
            method: 'post',
            url: '/sr/storeroomData!getDeptCheckNum.action',
            data: {}
          })
          .then((response) => {
            commit('setDepartCheckNum', response.data);
            resolve(response)
          }).catch(err => {
              reject(err)
          })
      })
    },
    //获取总务待审核数量
    getZWCheckNum ({ commit }) {
      return new Promise((resolve, reject) => {
        BaseAxios(
          {
            method: 'post',
            url: '/sr/storeroomData!getZWCheckNum.action',
            data: {}
          })
          .then((response) => {
            commit('setGeneralCheckNum', response.data);
            resolve(response)
          }).catch(err => {
            reject(err)
          })
      })
    },
    //获取物品发放数量
    getProvideNum ({ commit }) {
      return new Promise((resolve, reject) => {
        BaseAxios(
          {
            method: 'post',
            url: '/sr/storeroomData!getApplyReceiveRecordUnableNum.action?operationCode=sr_warehouseManage',
            data: {}
          })
          .then((response) => {
            commit('setProvideNum', response.data);
            resolve(response)
          }).catch(err => {
          reject(err)
        })
      })
    },
  }
})



