import {i18n} from "../../lang";

export const Error = [
  // error
  {
    path: '*',
    name: 'error_404',
    meta: {
      title: '404-'+i18n.t('notFound')
    },
    component: (resolve) => require(['./404.vue'], resolve)
  },
  {
    path: '/401',
    name: 'error_401',
    meta: {
      title: '401-'+i18n.t('notPermission')
    },
    component: (resolve) => require(['./401.vue'], resolve)
  },
  {
    path: '/500',
    name: 'error_500',
    meta: {
      title: '500-'+i18n.t('serverError')
    },
    component: (resolve) => require(['./500.vue'], resolve)
  }
]



