<!--组件式二级菜单，切换后刷新页面不会回到默认的tab页签-->
<template>
  <div class="zd-common-hp100">
    <div class="zd-common-h58 zd-common-lh58 zd-common-childMenu-bb zd-common-plr18">
      <ul class="clearfix zd-common-assist-font-c zd-common-font-size-large zd-common-nav zd-common-child-nav">
        <li v-for="item in menuList" @click="changeHeadNav(item.path,item.childName)"
             class="zd-common-fl zd-common-ml12 zd-common-cp zd-common-nav-child">
          <span :class="[curHeadNavActive === item.childName ? 'zd-common-active' : '']">
            {{ item.name }} {{item.number ? '('+item.number+')' : ''}}
          </span>
        </li>
      </ul>
    </div>
    <div class="zd-cm-content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    props:{
      menuList:{
        type:Array
      }
    },
    data() {
      return {
        curHeadNavActive: '',
      }
    },
    created() {
      this.changeHeadNav(this.$route.path, this.$route.meta.childName);
    },
    watch: {
      // 如果路由有变化，会再次执行该方法
      '$route': 'watchHandle'
    },
    methods: {
      watchHandle() {
        this.curHeadNavActive = this.$route.meta.childName;
      },
      changeHeadNav(path, name) {
        this.toMenu(path);
        this.curHeadNavActive = name;
      },
      //路由跳转
      toMenu(path) {
        this.$router.push(path);
      },
    }
  }
</script>

<style lang="less" scoped>
  .zd-cm-content{
    height: calc(~"100% - 60px");
  }
</style>



