import {utils} from "../../comm/script/utils";

export default {
  install (Vue, options) {
    // Vue.prototype.serverPath = 'http://192.168.1.233:14000/dc-storeroom-mysql';
    Vue.prototype.serverPath = setAxiosBaseUrl(modulePath.consumablesManage);
    Vue.prototype.dateRange = {
      shortcuts: [
        {
          text: '最近一周',
          value() {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            return [start, end];
          }
        },
        {
          text: '最近一月',
          value() {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            return [start, end];
          }
        },
        {
          text: '最近三月',
          value() {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            return [start, end];
          }
        }
      ]
    };
    //弹出提示
    Vue.prototype.postErrorTip = function (text) {
      this.$Message.destroy()
      this.$Message.error(text)
    }
    Vue.prototype.postWarningTip = function (text) {
      this.$Message.destroy()
      this.$Message.warning(text)
    }
    Vue.prototype.postSuccessTip = function (text) {
      this.$Message.destroy()
      this.$Message.success(text)
    }
    //流程出现横线滚动条，动态计算宽度
    Vue.prototype.calcWidth = function () {
      let w = 40;
      $(".zd-cur-step").each(function (i,item) {
        w += ($(this).actual('innerWidth')+parseInt($(this).css('marginLeft')))
      });
      $(".zd-cm-step").css({
        width:w+"px"
      });
    }
    //获取所有学校
    Vue.prototype.getAllSchool = function () {
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getAllSchools.action',
          data:{}
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //根据学校获取仓库地址
    Vue.prototype.getAllWarehouse = function (schoolId) {
      BaseAxios({
        method: 'post',
        url:'/sr/storeroomData!getAllClassroomsBySchoolId.action',
        data:{
          schoolId:schoolId
        },
      })
        .then((response) => {
          this.warehouseArr = response.data;
        })
        .catch((error) => {
          this.postErrorTip('请求失败');
          console.log(error);
        });
    }
    //验证编码
    Vue.prototype.validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('不能为空'));
      } else {
        if(utils.regCode.test(value)){
          BaseAxios(
            {
              method: 'post',
              url: '/sr/storeroomData!checkStoreroomCodeRepeat.action',
              data: {
                code:value,
                id:this.editId
              }
            })
            .then((response) => {
              if(response.data){
                callback(new Error('编码重复'));
              }
              callback();
            })
            .catch((error) => {
              callback(new Error('验证失败'));
              console.log(error);
            });
        }else{
          callback(new Error(utils.regCodeMsg));
        }
      }
    }
    //获取物品分类树
    Vue.prototype.getGoodsKindTree = function () {
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getGoodsKindTree.action',
          data:{}
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //递归构建树
    Vue.prototype.recursionFnOne = function(obj,arr){
      for(let i=0; i<arr.length; i++){
        if(arr[i].children){
          obj.push({
            id: arr[i].id,
            label: arr[i].name,
            children:[]
          });
          this.recursionFnOne(obj[i].children,arr[i].children);
        }else{
          obj.push({
            id: arr[i].id,
            label: arr[i].name
          })
        }
      }
    }
    Vue.prototype.recursionFnTwo = function(obj,arr){
      for(let i=0; i<arr.length; i++){
        if(arr[i].children){
          if(arr[i].id === "latest"){
            obj.push({
              id: arr[i].id,
              title: arr[i].name,
              expand: true,
              children:[],
              selected:true
            });
          }else{
            obj.push({
              id: arr[i].id,
              title: arr[i].name,
              expand: true,
              children:[]
            });
          }

          this.recursionFnTwo(obj[i].children,arr[i].children);
        }else{
          if(arr[i].id === 'latest'){
            obj.push({
              id: arr[i].id,
              title: arr[i].name,
              selected:true
            })
          }else{
            obj.push({
              id: arr[i].id,
              title: arr[i].name
            })
          }
        }
      }
    }
    //选择负责人
    Vue.prototype.selectLeader = function(name){
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getTeacherByParam.action',
          data:{
            param:name
          }
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //选择部门
    Vue.prototype.getDepartmentTree = function(id){
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getDepartmentTree.action',
          data:{
            id:id
          }
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //获取领用类型
    Vue.prototype.getApplyKindList = function() {
      BaseAxios({
        method: 'post',
        url: '/sr/storeroomData!getApplyKindList.action',
        data:{},
      })
        .then((response) => {
          this.typeArr = response.data;
        })
        .catch((error) => {
          this.postErrorTip('请求失败');
          console.log(error);
        });
    }
    //获取部门
    Vue.prototype.getDepartment = function(){
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getUserDepartments.action',
          data:{}
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //根据部门获取审核人
    Vue.prototype.getCheckUserByDepartmentId = function(departmentId) {
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getCheckUserByDepartmentId.action',
          data:{
            departmentId:departmentId
          }
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }

    //获取物品类型树 //useType 0--入库  1--出库 2--申领
    Vue.prototype.getGoodsKindTreeByUseType = function(type){
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getGoodsKindTreeByUseType.action',
          data:{
            useType:type
          }
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //获取出入库可选物品 //useType 0--入库  1--出库  2--申领
    Vue.prototype.getGoodsInfoByUseType = function(type,id,name,code){
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getGoodsInfoByUseType.action',
          data:{
            useType:type,
            goodsKindId:id,
            name:name,
            code:code
          }
        })
        .then((response) => {
          resolve(response)
        }).catch(err => {
          reject(err)
          this.postErrorTip('请求失败')
        })
      })
    }
    //补零
    Vue.prototype.fillZero = function(val){
      if(val<10){
        return '0'+val;
      }else{
        return val;
      }
    }
    //获取自己是库管的仓库
    Vue.prototype.getChargedStoreRoom = function() {
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getChargedStoreRoom.action',
          data:{}
        })
        .then((response) => {
          resolve(response)
        })
        .catch(err => {
          reject(err)
          this.postErrorTip('请求失败')
        })
      })
    }
    //获取入库类型
    Vue.prototype.getIntoKindList = function() {
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getIntoKindList.action',
          data:{}
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //获取出库类型
    Vue.prototype.getOutKindList = function() {
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getOutKindList.action',
          data:{}
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //获取供应商
    Vue.prototype.getGoodsSupplierAllEnable = function() {
      return new Promise((resolve, reject) => {
        BaseAxios({
          method:'post',
          url:'/sr/storeroomData!getGoodsSupplierAllEnable.action',
          data:{}
        })
          .then((response) => {
            resolve(response)
          })
          .catch(err => {
            reject(err)
            this.postErrorTip('请求失败')
          })
      })
    }
    //自定义分类
    Vue.prototype.limitHandle = function(count){
      return "+ "+count+"...";
    }
  }
}



