
/**
 * 系统公用方法
 *
 */
import {i18n} from '../../lang/index'

export const utils = {
  regPwd: /^[^\u4E00-\u9FFF|\s]{1,20}$/i,//验证密码
  regPwdMsg:i18n.t('regPwdMsg'),//验证密码提示
  regUser:/^[^\u4E00-\u9FFF|\s]+$/i,//验证用户名
  regUserMsg:"请输入数字、字母、符号",//验证用户名提示
  regCode:/^[^\u4E00-\u9FFF|\s]+$/i,//验证编码
  regCodeMsg:"请输入数字、字母、符号",//验证编码提示
  regUrl:/^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\*\+,;=.]+$/,//验证url
  regUrlMsg:"地址格式不正确",//验证url提示
  regImportType:/(.xls|.xlsx)/i,//验证导入文件类型
  regImportTypeMsg:"请上传.xls|.xlsx类型的excel文件",//验证导入文件类型提示
  regPicType:/(.jpg|.jpeg|.png)/i,//验证图片类型
  regPicMsg:"请上传.jpg|.jpeg|.png类型的图片",//验证图片类型提示
  regPicSize:2*1024*1024,//图片大小
  regPicSizeMsg:"图片太大,最多上传2M大小的图片",//超过图片大小提示
  regPicNum:5,//图片数量
  regPicNumMsg:"最多上传5张",//超出图片数量提示
  getQueryString(name) {
    const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
    const r = window.location.search.substr(1).match(reg) || window.location.hash.substring((window.location.hash.search(/\?/)) + 1).match(reg);
    if (r != null) {
      return decodeURIComponent(r[2]);
    }
    return '';
  },
  getRootPath() {
    var curWwwPath = window.document.location.href;
    var pathName = window.document.location.pathname;
    var pos = curWwwPath.indexOf(pathName);
    return curWwwPath.substring(0, pos);
  },
};

/**
 * 权鉴
 * @param {*} name 即将跳转的路由name
 * @param {*} access 用户权限数组
 * @param {*} routes 路由列表
 * @description 用户是否可跳转到该页
 */
export const canTurnTo = (name, access, routes) => {
  const routePermissionJudge = (list) => {
    return list.some(item => {
      if (item.children && item.children.length) {
        return routePermissionJudge(item.children)
      } else if (item.name === name) {
        return hasAccess(access, item)
      }
    })
  }
  return routePermissionJudge(routes)
}

/**
 * @param {*} access 用户权限数组，如 ['super_admin', 'admin']
 * @param {*} route 路由列表
 */
export const hasAccess = (access, route) => {
  if (route.meta && route.meta.roles) return hasOneOf(access, route.meta.roles)
  else return true
}

/**
 * @param {Array} target 目标数组
 * @param {Array} arr 需要查询的数组
 * @description 判断要查询的数组是否至少有一个元素包含在目标数组中
 */
export const hasOneOf = (targetarr, arr) => {
  return targetarr.some(_ => arr.indexOf(_) > -1)
}
/**
 * 转换拼音
 *
 *
 */
let pinyin = require('pinyin');
export const transformPY = function(val){
  return pinyin(val,{
    style: pinyin['STYLE_FIRST_LETTER'],//首字母风格，只返回拼音的首字母部分
    // heteronym: true,// 启用多音字模式
    // segment: true // 启用分词，以解决多音字问题。
  }).join('')
}



